/*
 * Initialize.css is a complete and customizable collection of CSS best practices based on Normalize.css and HTML5 Boilerplate.
 * http://jeroenoomsnl.github.io/initialize-css
 *
 * HTML5 Boilerplate: https://github.com/h5bp/html5-boilerplate
 * Normalize.css: http://github.com/necolas/normalize.css
 */

//
// Predefined values
//
$initialize-box-sizing: border-box !default;
$initialize-font-family: sans-serif !default;
$initialize-font-size: 1em !default;
$initialize-line-height: 1.4 !default;

//
// Options
//
$initialize-dinosaur-mode: true !default; // toggle support for IE8 & IE9

//
// Optional values
//
$initialize-color: null !default;
$initialize-heading-color: null !default;
$initialize-heading-margins: null !default;
$initialize-heading-font-family: null !default;
$initialize-block-margins: null !default;

//
// Mixin for optional properties
//
@mixin optional-prop($property, $value) {
  @if $value != null {
    #{$property}: $value;
  }
}

//
// General
//
html {
  @include optional-prop('color', $initialize-color);
  @include optional-prop('box-sizing', $initialize-box-sizing);

  font-family: $initialize-font-family;
  line-height: $initialize-line-height;
  font-size: $initialize-font-size;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

//
// HTML5 display defenitions
//
@if $initialize-dinosaur-mode == true {
  // Correct `block` display not defined for any HTML5 element in IE 8/9.
  article,
  aside,
  details,
  figcaption,
  figure,
  footer,
  header,
  hgroup,
  main,
  menu,
  nav,
  section,
  summary {
    display: block;
  }
} @else {
  // Correct `block` display not defined for `details` or `summary` in IE 10/11 and Firefox.
  // Correct `block` display not defined for `main` in IE 11.
  details,
  main,
  summary {
    display: block;
  }
}

audio,
canvas,
progress,
video {
  // Correct `inline-block` display not defined in IE 8/9.
  @if $initialize-dinosaur-mode == true {
    display: inline-block;
  }
  // Normalize vertical alignment of `progress` in Chrome, Firefox, and Opera.
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

//
// Links
//
a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

//
// Text-level semantics
//
abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

@if $initialize-dinosaur-mode == true {
  // Address styling not present in IE 8/9.
  mark {
    background: #ff0;
    color: #000;
  }
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

//
// Embedded content
//
img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

//
// Grouping content
//
figure {
  margin: 1em 40px;
}

hr {
  box-sizing: inherit;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

//
// Forms
//
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
  box-sizing: inherit;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: inherit;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

fieldset {
    border: 0;
    padding: 0;
}

textarea {
    resize: vertical;
}

//
// Typography
//
h1, h2, h3, h4, h5, h6 {
  @include optional-prop('color', $initialize-heading-color);
  @include optional-prop('margin', $initialize-heading-margins);
  @include optional-prop('font-family', $initialize-heading-font-family);
}

p, table, ul, ol, fieldset, blockquote, dl {
  @include optional-prop('margin', $initialize-block-margins);
}
